// Z_INDEX

.z-10 {
	z-index: 10 !important;
}



// FOOTER

.footer-logo-wrapper {
	svg {
		@include media-breakpoint-down(xs) {
			width: 200px;
			height: auto;
		}
		@include media-breakpoint-only(sm) {
			width: 250px;
			height: auto;
		}
	}
}

// add additional space to not overlay bottom fixed menu
.to-top-wrapper {
	@include media-breakpoint-down(sm) {
		padding-bottom: 3.75rem;
	}
}
// posituin bottom fixed menu above to top button
.bsx-mobile-fixed-wrapper {
	z-index: $zindex-sticky;
}



// BOKING FORM

.rtb-booking-form {

	// row
	form > fieldset {
		display: flex;
		flex-wrap: wrap;
		margin-right: calc($grid-gutter-width / -2);
		margin-left: calc($grid-gutter-width / -2);
	}

	// col 12
	fieldset > *,
	fieldset > legend {
		position: relative;
		width: 100%;
		padding-right: calc($grid-gutter-width / 2);
		padding-left: calc($grid-gutter-width / 2);
		margin-top: 0 !important;
	}
	@include media-breakpoint-up(sm) {
		// col 4
		legend ~ div[class^="rtb-"] {
			flex: 0 0 33.3333333333%;
			max-width: 33.3333333333%;
		}
	}

	// headings
	legend {
		margin-bottom: 0;
		line-height: 1.25;
		@extend .h5;
		@extend .text-inherit;
	}
	fieldset + fieldset > legend {
		@extend .pt-4;
	}

	fieldset {
		padding-bottom: 0 !important;
		margin-bottom: 0 !important;
	}

	label {
		margin-top: .6rem;
		margin-bottom: .2rem;
	}

	// controls
	input,
	select,
	textarea {
		width: 100%;
		max-width: unset !important;
		@extend .form-control;
	}

	.add-message {
		@extend .mt-3;
	}

	.rtb-form-submit {
		margin-top: 0 !important;
	}

	[type="submit"] {
		@extend .btn;
		@extend .btn-primary;
		@extend .btn-lg;
	}

	// message
	.rtb-message {
		@extend .alert;
		@extend .alert-warning;

		> p:last-child {
			margin-bottom: 0;
		}
	}

}


// MOBILE FIXED MENU

.header-shadow {
	box-shadow: 0 -1px 0 0 rgba(255, 255, 255, 0.2), 0 0 15px 0 rgba(255, 255, 255, 0.1);
}


